import React from 'react';
import { 
  Box, 
  Typography, 
  Paper, 
  Grid, 
  LinearProgress
} from '@mui/material';
import { Personnage, STAT_MAX_CREATION, STAT_MAX_JEU } from '../../models/types';

interface StatsMenuProps {
  personnage: Personnage;
}

// Fonction pour obtenir la couleur en fonction de la valeur de la statistique
const getStatColor = (value: number): string => {
  // Normaliser la valeur par rapport  STAT_MAX_JEU
  const normalizedValue = value / STAT_MAX_JEU * 10;
  
  if (normalizedValue <= 3) return '#e74c3c'; // Faible - rouge
  if (normalizedValue <= 6) return '#f1c40f'; // Moyen - jaune
  if (normalizedValue <= 8) return '#2ecc71'; // Bon - vert
  return '#9b59b6'; // Excellent - violet
};

// Composant pour afficher une statistique avec une barre de progression
const StatDisplay = ({ nom, valeur, description }: { nom: string, valeur: number, description: string }) => {
  // Calculer le pourcentage pour la barre de progression (0-100%)
  const progressValue = (valeur / STAT_MAX_JEU) * 100;
  
  return (
    <Box sx={{ mb: 2 }}>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 0.5 }}>
        <Typography variant="body1">{nom}</Typography>
        <Typography variant="body1" fontWeight="bold">
          {valeur} {valeur >= STAT_MAX_CREATION && `/ ${STAT_MAX_JEU}`}
        </Typography>
      </Box>
      <LinearProgress 
        variant="determinate" 
        value={progressValue} 
        sx={{ 
          height: 8, 
          borderRadius: 4,
          backgroundColor: 'rgba(255, 255, 255, 0.1)',
          '& .MuiLinearProgress-bar': {
            backgroundColor: getStatColor(valeur),
          }
        }} 
      />
      <Typography variant="body2" color="text.secondary" sx={{ mt: 0.5 }}>
        {description}
      </Typography>
    </Box>
  );
};

const StatsMenu: React.FC<StatsMenuProps> = ({ personnage }) => {
  // Calcul du total des statistiques de base uniquement (sans les stats drives)
  const statsDeBase = [
    personnage.stats.force,
    personnage.stats.agilite,
    personnage.stats.constitution,
    personnage.stats.intelligence,
    personnage.stats.perception,
    personnage.stats.charisme,
    personnage.stats.chance,
    personnage.stats.qi
  ];
  
  const totalStats = statsDeBase.reduce((a, b) => a + b, 0);
  const moyenneStats = totalStats / statsDeBase.length;

  // Dterminer la couleur de la moyenne en fonction de sa valeur par rapport  STAT_MAX_CREATION
  // pour la cration du personnage, ou STAT_MAX_JEU pour les personnages avancs
  const maxStatReference = Math.max(...statsDeBase) > STAT_MAX_CREATION ? STAT_MAX_JEU : STAT_MAX_CREATION;
  const moyenneColor = getStatColor(moyenneStats);

  return (
    <Box>
      <Typography variant="h5" component="h1" gutterBottom>
        Statistiques
      </Typography>
      
      <Paper elevation={3} sx={{ p: 3, backgroundColor: 'background.paper', mb: 3 }}>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
          <Typography variant="h6">Aperu des Statistiques</Typography>
          <Box sx={{ 
            px: 1.5, 
            py: 0.5, 
            borderRadius: 1, 
            backgroundColor: moyenneColor,
            color: 'white',
            fontWeight: 'bold'
          }}>
            Moyenne: {moyenneStats.toFixed(1)}
          </Box>
        </Box>
        
        <Typography variant="body2" color="text.secondary" paragraph>
          Les statistiques dterminent vos capacits et influencent votre progression dans le monde de la cultivation.
        </Typography>
        
        <Grid container spacing={3}>
          <Grid item xs={12} md={6}>
            <StatDisplay 
              nom="Force" 
              valeur={personnage.stats.force} 
              description="Dtermine votre puissance physique et vos dgts au combat."
            />
            <StatDisplay 
              nom="Agilit" 
              valeur={personnage.stats.agilite} 
              description="Affecte votre vitesse, votre esquive et votre prcision."
            />
            <StatDisplay 
              nom="Constitution" 
              valeur={personnage.stats.constitution} 
              description="Influence votre sant, votre endurance et votre rsistance."
            />
            <StatDisplay 
              nom="Intelligence" 
              valeur={personnage.stats.intelligence} 
              description="Amliore votre capacit  apprendre des techniques et augmente votre gain de Qi."
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <StatDisplay 
              nom="Perception" 
              valeur={personnage.stats.perception} 
              description="Affecte votre capacit  dtecter les dangers et les opportunits."
            />
            <StatDisplay 
              nom="Charisme" 
              valeur={personnage.stats.charisme} 
              description="Influence vos interactions sociales et votre capacit  diriger."
            />
            <StatDisplay 
              nom="Chance" 
              valeur={personnage.stats.chance} 
              description="Augmente vos chances de trouver des objets rares et d'viter les dangers."
            />
            <StatDisplay 
              nom="Qi" 
              valeur={personnage.stats.qi} 
              description="Dtermine votre puissance spirituelle et votre taux de gain de Qi pendant la mditation."
            />
          </Grid>
        </Grid>
      </Paper>
      
      <Paper elevation={3} sx={{ p: 3, backgroundColor: 'background.paper' }}>
        <Typography variant="h6" gutterBottom>Effets des Statistiques</Typography>
        
        <Box sx={{ mb: 2 }}>
          <Typography variant="body2" gutterBottom>
            <strong>Gain de Qi par seconde:</strong> {personnage.stats.qi} (base) + {(personnage.stats.intelligence * 0.08).toFixed(2)} (intelligence) + {(personnage.stats.perception * 0.04).toFixed(2)} (perception)
          </Typography>
          
          <Typography variant="body2" gutterBottom>
            <strong>Esprance de vie:</strong> Influence par votre race et votre niveau de cultivation.
          </Typography>
          
          <Typography variant="body2" gutterBottom>
            <strong>Combat:</strong> Force, Agilit et Constitution dterminent vos capacits au combat.
          </Typography>
          
          <Typography variant="body2" gutterBottom>
            <strong>Cultivation:</strong> Intelligence, Perception et Qi influencent votre progression spirituelle.
          </Typography>
          
          <Typography variant="body2" gutterBottom>
            <strong>Social:</strong> Charisme et Chance affectent vos interactions et dcouvertes.
          </Typography>
        </Box>
        
        <Box sx={{ 
          p: 2, 
          backgroundColor: 'rgba(0,0,0,0.1)', 
          borderRadius: 1,
          border: '1px solid #a8dadc'
        }}>
          <Typography variant="body2" gutterBottom>
            <strong>Conseil:</strong> Pour maximiser votre gain de Qi, concentrez-vous sur l'amlioration de votre Intelligence, Perception et Qi.
          </Typography>
        </Box>
      </Paper>
    </Box>
  );
};

export default StatsMenu; 